
import Divider from 'vux/src/components/divider/index.vue'

import Popup from 'vux/src/components/popup/index.vue'


export default {
data() {
return {
isLoading: true,
detail: {
last_price: 0
},
id: '',
doStart: false,
startPrice: 0,
incr_price: 0,
showPrice:false,
promisePirce:200,
offerPirce:'',//出价金额
novali:false,
ispromise:false,
hasPaypromise:false,
menus1: {
menu1: '<span style="color:green">支付保证金</span>'
}


}
},
beforeCreate() {

this.isLoading = true
this.$http.get('/api/getgoodInfo?id=' + this.$route.params.id)
.then((res) => {
this.detail = res.data.data;
document.title = this.detail.title
this.isLoading = false


})
.catch((err) => {
console.log(err)
})

},
created() {},
mounted() {

this.priceHistory()

},
methods: {

menuClick(key){
if('menu1' == key){
this.$router.push('/pay_promise/'+this.$route.params.id)
}
},


offer(){


if(this.offerPirce.length<1)
{
this.novali = true
return

}

var params = new URLSearchParams();
params.append('id', this.$route.params.id);       
params.append('price', this.offerPirce);

this.$http.post('/api/setGoodOffer',params).then((res)=>{


if(res.data.errorno == 0){
this.priceHistory()
}


})



},

show(){

if(!this.hasPaypromise){
this.ispromise = true
}else{
this.showPrice = true
}
},

gavePrice(){

},

priceHistory(){
this.$http.get('/api/getGoodOfferList?id='+this.$route.params.id).then((res)=>{

console.log('出价列表',res.data)

this.historyData= res.data

})
},

},
computed: {
isNum() {
return parseInt(this.detail.incr_price);
}
},
components: {
Divider,
Popup
}
}




