import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router'
import store from './store'
import './assets/css/common.css'
import footerBar from '@/components/footer'

import { sync } from 'vuex-router-sync'
sync(store, router)

Vue.component('footer-bar',footerBar)



import WechatPlugin from 'vux/src/plugins/wechat/index.js'

Vue.use(WechatPlugin)


Vue.use(require('vue-cookies'))

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'



Vue.use(AjaxPlugin)
Vue.component('toast', Toast)
Vue.component('toast', Toast)
Vue.component('card', Card)

Vue.component('actionsheet', Actionsheet)

Vue.directive('transfer-dom', TransferDom)
Vue.component('countup', Countup)
Vue.component('clocker', Clocker)
Vue.component('loading', Loading)
Vue.component('x-header', XHeader)
Vue.component('x-button', XButton)
Vue.component('cell-box', CellBox)
Vue.component('cell', Cell)
Vue.component('group', Group)
Vue.component('x-input', XInput)
Vue.component('popup', Popup)

Vue.directive('title', {
inserted: function (el, binding) {
document.title = el.dataset.title
}
})


router.beforeEach((to, from, next) => {
if(!store.state.user.id && to.path != '/author'){

window.$cookies.set('beforeLoginUrl', to.fullPath) 
next('/author')
return false
}
next()
})


Vue.prototype.goBeforeLoginUrl = () => {
let url = window.$cookies.get('beforeLoginUrl')
if(!url || url.indexOf('/author') != -1){
router.push('/index')
}else{
if(url == '/'){
url = '/index'
}
router.push(url)
window.$cookies.set('beforeLoginUrl', '')
}
}


FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



